.schedule {
    background-color: $white;
    position: relative;
    margin-top: 95px;
    // max-width: 100vw;
    width: 100%;
    // overflow: hidden;
    &::before {
        display: block;
        //   opacity: 0;
        position: absolute;
        z-index: 1;
        content: "";
        top: -95px;
        left: 0;
        width: 100%;

        height: 95px;
        background-image: url(../images/schedule-bg1.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position-y: top;
        background-position-x: right;
    }
    &__container {
    }

    &__top {
        padding-top: 45px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;

        @media screen and (max-width: 500px) {
            flex-direction: column;
            gap: 40px 0;
        }
    }

    &__title {
        color: $black;
        font-size: 40px;
        font-weight: 600;
        text-transform: uppercase;
    }

    &__link {
        max-width: 178px;
        width: 100%;
        display: inline-flex;
        gap: 0 5px;
        align-items: center;
        justify-content: center;
        min-height: 60px;
        height: auto;
        border: 2px solid $black;
        padding: 10px;

        text-transform: uppercase;
        font-size: 16px;

        span:first-child {
            color: $black;
        }
        span:last-child {
            color: $red;
        }
        span {
            @include transition;
        }
        @include transition;
        &:hover {
            background-color: $black;

            span:first-child {
                color: $white;
            }
        }
        @media screen and (max-width: 500px) {
            max-width: 100%;
        }
    }

    &__triggers {
        display: flex;
        // flex-wrap: wrap;
        gap: 10px;
        overflow: auto;
        margin-bottom: 30px;
        overflow-x: auto;
        padding-bottom: 10px;
        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }
        &::-webkit-scrollbar-button {
            width: 0px;
            height: 0px;
        }
        &::-webkit-scrollbar-thumb {
            background: $grey;
            border: 0px none $grey;
            border-radius: 50px;

            @include transition;
        }
        &::-webkit-scrollbar-thumb:hover {
            background: $red;
        }
        &::-webkit-scrollbar-thumb:active {
            background: $red;
        }
        &::-webkit-scrollbar-track {
            background: #ffffff;
            border: 0px none #ffffff;
            border-radius: 19px;
        }
        // &::-webkit-scrollbar-track:hover {
        //     background: #666666;
        // }
        // &::-webkit-scrollbar-track:active {
        //     background: #333333;
        // }
        // &::-webkit-scrollbar-corner {
        //     background: transparent;
        // }

        // &::-webkit-scrollbar {
        //     scrollbar-width: none; /* для Firefox */
        //     -ms-overflow-style: none;
        //     display: none;
        // }
    }

    &__triggers-btn {
        padding: 5px 4px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #616161;
        font-size: 16px;
        text-transform: uppercase;
        @include transition;
        min-height: 34px;
        height: auto;
        max-width: 130px;
        width: 100%;

        white-space: nowrap;
        &:hover,
        &.active {
            background-color: $red;
            color: $white;
        }
        // overflow-x: auto;
    }

    &__content {
        display: flex;
        align-items: stretch;
        gap: 18px;
        flex-wrap: wrap;
    }

    &__event {
        flex: 0 1 264px;
        display: flex;
        flex-direction: column;
        min-height: 474px;
        height: auto;
        @media screen and (max-width:768px){
            flex: 1 1 264px;
        }
    }

    &__event-picture {
        position: relative;
        aspect-ratio: 1/1;

        margin-bottom: 20px;
    }

    &__event-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }

    &__event-description {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
    }

    &__event-title {
        line-height: 120%;
        font-size: 20px;
        font-weight: 500;
        color: $black;
        margin-bottom: 10px;
    }

    &__event-date {
        display: inline-flex;
        gap: 0 10px;
        margin-bottom: 10px;
        font-size: 18px;
        span:first-child {
            color: #7e7e7e;
            font-weight: 300;
        }
        span:last-child {
            color: $red;
        }
    }

    &__event-place {
        font-size: 20px;
        color: $black;
    }

    &__event-link {
        margin-top: auto;
        display: inline-flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        border: 2px solid $black;
        font-size: 16px;

        @include transition;
        &:hover {
            border: 2px solid $red;
            color: $red;
        }
    }
}
